import { useAsyncRetry } from "react-use";
import { useApi } from "@backstage/core-plugin-api";
import { Build, Project } from "@aws-sdk/client-codebuild";
import { awsCodeBuildApiRef } from "../api/AwsCodeBuildApi";

export function useCodeBuildBuilds(arn: string) {
  const api = useApi(awsCodeBuildApiRef);

  const { loading, value, error, retry } = useAsyncRetry<{
    project: Project;
    builds: Build[];
  }>(async () => {
    const project = await api.getProject({ arn });
    const builds = await api.listBuilds({ arn });

    if (project.projects) {
      if (project.projects.length === 1) {
        return { project: project.projects[0], builds };
      }
    }

    throw new Error("CodeBuild project could not be retrieved");
  }, []);

  return {
    loading,
    builds: value?.builds,
    project: value?.project,
    error,
    retry,
  } as const;
}
